.single {
    display: flex;
    width: 100%;

    .singleContainer {
        flex: 6;

        .top {
            padding: 20px;
            display: flex;
            gap: 20px;
            
            .left{
                flex:1;
                -webkit-box-shadow: 2px 4px 10px 1px rgba(0,0,0,0.4);
                box-shadow: 2px 4px 10px 1px rgba(0,0,0,0.4);
                padding: 20px;
                position: relative;

                .editButton {
                    position: absolute;
                    top: 0;
                    right: 0;
                    padding: 5px;
                    font-size: 12px;
                    color: #e88607;
                    background-color: #e887073d;
                    cursor: pointer;
                    border-radius: 0 0 0 5px;
                }

                .item {
                    display: flex;
                    gap: 20px;

                    .itemImg {
                        width: 100px;
                        height: 100px;
                        border-radius: 50%;
                        object-fit: cover;
                    }

                    .details {
                        .itemTitle {
                            margin-bottom: 10px;
                            color: gray;
                        }

                        .detailItem {
                            margin-bottom: 5px;
                            font-size: 14px;

                            .itemKey {
                                font-weight: bold;
                                color:gray;
                                margin-right: 5px;
                            }
                        }
                    }
                }
            }
            .right{
                flex: 2
            }
        }
        .bottom {
            padding: 20px;
            -webkit-box-shadow: 2px 4px 10px 1px rgba(0,0,0,0.4);
            box-shadow: 2px 4px 10px 1px rgba(0,0,0,0.4);
            margin: 10px 20px;
        }    
    
    }

    
    .title {
        font-size: 16px;
        color: lightgray;
        margin-bottom: 20px;
    }
}