.new {
    width: 100%;
    display: flex;

    .newContainer {
        flex: 6;

        .top, .bottom{
            padding: 20px;
            -webkit-box-shadow: 2px 4px 10px 1px rgba(0,0,0,0.4);
            box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
            margin: 10px 20px;
            display: flex;

            h1 {
                color: lightgray;
                font-size: 20px;
            }

            .left {
                flex: 1;
                text-align: center;

                img {
                    width: 100px;
                    height: 100px;
                    border-radius: 50%;
                    object-fit: cover;
                }
            }
            .right {
                flex: 2;

                form {
                    display: flex;
                    flex-wrap: wrap;
                    gap: 20px;
                    justify-content: space-between;

                    .selectRooms {
                        width: 100%;

                        select {
                            width: 100%;
                            padding: 10px;
                        }
                    }

                    .formInput {
                        width: 40%;
                        display: flex;
                        flex-direction: column;

                        label  {
                            display: flex;
                            align-items: center;
                            gap: 10px;

                            .icon  {
                                cursor: pointer;
                            }
                        }

                        input {
                            display: 100%;
                            padding: 5px;
                            border: none;
                            border-bottom: 1px solid lightgray;
                        }
                    }

                    button {
                        width: 20%;
                        padding: 10px;
                        border: none;
                        background-color: teal;
                        color: white;
                        font-weight: bold;
                        cursor: pointer;
                        margin-top: 10px;

                        &:hover {
                            background-color: orange;
                        }

                        &:active {
                            scale: 0.95;
                        }
                    }
                }
            }
        }
    }
}